.wrapper{
    width: 100%;
    display: flex;
    align-items:flex-start;
    justify-content: center;
    flex-wrap: wrap;
    margin: auto;
}
.about-container{
    /* z-index: -1; */
    opacity: 0.8;
    height: auto;
    min-height: 100vh;
    padding: 5% 5% 5% 10%;
    width: 60%;
    background-color:#ffd369;
    display: flex;
    align-items:flex-start;
    justify-content: center;
    flex-wrap: wrap;
    flex-direction: column;
    position:absolute;
    right: 0;
    margin: auto;
}

.image-container{
    background-image: url("../images/profile-image_bw.png");
    background-repeat: no-repeat;
    background-size: cover;
    background-color: black;
    height: 100vh;
    width: 40%;
    display: flex;
    align-items:flex-end;
    justify-content: center;
    flex-wrap: wrap;
    flex-direction: column;
    position:fixed;
    left:0;
}

@media (max-width: 1024px){
    .about-container{
        flex-wrap:none;
        width: 80%; 
        height: auto;
        margin: 10% 0%;
        min-height: 100vh;
        padding: 10% 10%;
        position: static;
    
    }
    .image-container{
        width:100%;
        position: fixed;
        z-index: -1;
        flex-wrap: none;
    }
  }


.big-heading{
    color: black;
    line-height: 1.5;
    font-size: 40px;
}
.heading{
    color: black;
    line-height: 2.5;
    font-size: 20px;
    font-weight: bolder; 
}

.my-name{
    background-color: black;
    color:white;
}

.description{
    color: black;
    font-size: 14px;
}
